* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  max-width: 100%;
  vertical-align: middle;
}
.headerSection {
  position: relative;
  width: 100%;
  height: 64px;
  background-color: #20232a; 
}
.headerContainer {
  max-width: 1000px;
  margin: 0 auto 0 auto;
  padding: 0 14px;
}
.headerContent {
  position: relative;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.headerBrand {
  position: relative;
  width: 30%;

}
.headerBrandItem {
  padding: 18px 0;
}
.headerBrandTitle {
  margin : 0;
  color:#fff;
  font-size: 24px;
}
.headerBrandTitle a {
  color:#fff;
  text-decoration: none;
}
.headerNavigation {
  float: left;
  width: 70%;
}
.headerNavigation > ul {
  margin: 0;
  padding: 0;
  float: right;
}
.headerNavigation ul > li {
  display: inline-block;
  color: #fff;
}
.headerNavigation ul > li > a {
  position: relative;
  display: inline-block;
  color: #fff;
  line-height: 1.6;
  font-weight: bold;
  padding: 20px 14px;
  font-size: 16px;
  text-decoration: none;
}
.headerNavigation ul > li.active {
  background-color: #2f333e;
}
.headerNavigation ul > li.active > a {
  color: #0be881;
}
.headerNavigation ul > li.active > a::before {
  content: "";
  position: absolute;
  background-color: #0be881;
  height: 5px;
  width: 100%;
  bottom: 0;
  left: 0;
}
.contentSection {
  width: 100%;
}
.contentContainer {
  max-width: 1000px;
  margin : 0 auto;
  padding: 0 14px;
}
.mainSection {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.mainLeft {
  float: left;
  width: calc(100% - 300px);
  padding-right : 42px;
}
.mainRight {
  float: left;
  width: 300px;
}
.postListSection {
  padding: 62px 0;
}
.postList {
  margin-bottom: 24px;
}
.postListItem {
    position: relative;
    overflow: hidden;
}
.postListItem {
    padding: 14px 0;
}
.postListTitle {
    margin: 0;
    font-size: 32px;
    line-height: 1.4;
    margin-bottom: 14px;
}
.postListSnippet {
    line-height: 1.8;
}
.authorCard {
  position: relative;
  background-color: #f5f5f5;
  text-align: center;
  padding: 42px 14px 42px 14px;
  border-radius: 4px;
}
.authorPic {
  margin : 0 0 14px 0;
  border-radius: 50%;
  max-width: 100%;
  width: 120px;
}
.authorText {
  width: 100%;
}

.authorName {
  font-size: 18px;
  font-weight: 600;
}
.sidebarSection {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  padding: 64px 0;
}
.footerSection {
  position: relative;
  background-color: #20232a;
  color: #fff;
}
.footerContainer {
  max-width: 1000px;
  margin: auto;
  padding: 0 14px;
}
.footerContent {
  padding: 24px 0;
}
.copyRightHolder {
  text-align: center;
}
.copyRightHolder p {
  margin: 0;
}
.articleSection {
  position: relative;
  padding: 64px 0;
  line-height: 1.6;
  word-break: keep-all;
}
.articleHeader {
  position: relative;
  padding-bottom: 24px;
  margin-bottom: 24px;
  /* border-bottom: 2px solid #ddd; */
}
.articleTitle {
  margin: 0;
  font-size: 42px;
  line-height: 1.2;
}

.articleBody {

}
blockquote {
  position: relative;
  margin: 0;
  background-color: #f5f5f5;
  padding:24px;
  margin: 24px 0;
  border : 1px solid #ddd;
  border-radius: 4px;
  color : #20232a;
}
pre {
  position: relative;
  overflow: hidden;
  background-color: #222;
  color:#fff;
  padding : 24px;
  overflow-x: auto;
  font-size: 14px;
  border-radius: 4px;
}
